
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},
components: {
XHeader,
Actionsheet
},
name: 'header',
data () {
return {
propUser: this.user,
header: {
leftOptions: {
showBack: false
},
rightOptions: {
showMore: true
},
pageStatus: 'Login',
showMenus: false
}
}
},
created () { },
methods: {
headerRightClick () {
this.header.showMenus = true
},
menuClick (menuKey, menuItem) {
console.log(menuKey)
console.log(JSON.stringify(menuItem))
}
},
computed: {
arragneMenus () {
if (this.propUser.isLogin) {
return [
'UserName: ' + this.propUser.userName, 'Name: ' + this.propUser.name, 'Email: ' + this.propUser.email, 'Mobile: ' + this.propUser.mobile, '退出登录/Logout']
} else {
return null
}
}
},
props: ['user']
}



